.App {
  text-align: center;  
  margin-left: auto;
  margin-right: auto;
  margin-top: 0;
  min-height: 50vh;
  align-items: center;
  justify-content: center;
  display: flex;  
  width: 50%;  
  /*border: 1px solid black;*/
}

body{  
  background-image: url('./green-hill-small.jpg');
  background-size: cover;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
