/*  Navigation bar  */
.navbar{
    overflow: hidden;
    /*background-color: #333;*/
    background-color: rgb(0, 0, 106);
    margin-bottom: 10px;
}
/*  Navigation item (main menu) in horizental   */
.navbar a{
    float: left;
    font-size: 16px;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}
/*  dropdown item but hidden. 
    If you want the width of the dropdown content to be as wide as the dropdown button, set the width to 100% (and overflow:auto to enable scroll on small screens).    */
.dropdown{
    float: left;
    overflow: hidden;
}
.dropdown .dropbtn{
    font-size: 16px;
    border: none;
    outline: none;
    color: white;
    padding: 14px 16px;
    background-color: inherit;
    font-family: inherit;
}
/*  When mouse over html element a in parent .navbar change bkcolor to other color. 
    This action show user that this menu has been focus */
.navbar a:hover, .dropdown:hover, .dropbtn{
    background-color: red;
}
/*  Dropdown content decoration. Normally we hide it when app started.
    z-index : 1 is make sure this drowpdown content is on top of everything */
.dropdown-content{
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;
}
/*  Dropdown item.   */
.dropdown-content a, .dropdown-content button{
    float: none;
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    text-align: left;
}
/*  Dropdown content inside main menu mouse over change bk color to white-gray */
.dropdown-content a:hover{
    background-color: #ddd;
}
/*  Dropdown menu (main menu) mouse over change color to red.
    Show the drop down menu */
.dropdown:hover .dropdown-content{
    display: block;
}

