*{
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 15px;
}

p{
    margin-left: 10px;
    margin-bottom:0;
    margin-top: 0;
    margin-right: 0;
    color: azure;    
}

button{
    border-radius: 5px;
    margin: 7px;
    border: rgb(255, 255, 255) solid 1px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 20px;
    padding-right: 20px;
    background-color: rgb(159, 159, 249);
}

input, input:focus{
    /*border-radius: 5px;*/
    padding: 10px;
    /*display: block;*/
    width: 95%;
    margin-top: 0px;
    margin-right: 7px;
    margin-bottom: 7px;
    margin-left: 7px;
    border: 1px solid darkgray;
    border-radius: 5px;
    /*border-top: none;
    border-left: none;
    border-right: none;
    border-bottom: 1px blue solid;*/    
}

input[type=datetime-local]{
    width: 15%;
}

select{
    padding: 10px;
    margin: 7px;
    width: 40%;
}

textarea{
    padding: 10px;
    margin: 7px;
    width: 70%;
    height: 30vh;    
}